import {noteNames, noteOrder, naturalNotes, chordTypes, noteSemitonesAhead, parseNoteLocations, sortStartingFromKey} from './notes'
import {chords} from './chords'

const {A, B, C, D, E, F, G, As, Cs, Ds, Fs, Gs, Ab, Bb, Db, Eb, Gb} = noteNames
const { Major, Minor, Diminished } = chordTypes

const chordTypesByInterval = {
  Major: [
    Major,
    Minor,
    Minor,
    Major,
    Major,
    Minor,
    Diminished
  ],
  Minor: [
    Minor,
    Diminished,
    Major,
    Minor,
    Minor,
    Major,
    Major
  ]
}

const cumulativeSemitoneOffsetsByInterval = {
  Major: [0, 2, 4, 5, 7, 9, 11],
  Minor: [0, 2, 3, 5, 7, 8, 10]
}

// this "interval" indexes from 0 unlike in music, but same idea
const chordsInKey = (root, type) => chordTypesByInterval[type].map((chordType, interval) => 
  chords.find(chord => chord.root === noteSemitonesAhead(root, cumulativeSemitoneOffsetsByInterval[type][interval]) &&
                       chord.type === chordType)
)

class KeySignature {
  constructor(root, type) {
    this.root = root
    this.type = type
    this.chords = chordsInKey(root, type)
  }

  get chordsAsIntervals() {
     // a very roundabout way of just getting the index, but this way it's consistent with other code instead of re-implementing, since this is just a helper function
    return this.chords.map(chord => chord.intervalLetterInKey(this))
  }

  get name() {
    return `${this.root} ${this.type}`
  }
}

const keyTypes = [Major, Minor]
const keySignatures = []
noteOrder.forEach(root => {
  keyTypes.forEach(type => {
    keySignatures.push(new KeySignature(root, type))
  })
})

const relativeMinorForKey = (relativeMajor) => keySignatures.find(key => {
  return key.root === noteSemitonesAhead(relativeMajor.root, 9) && key.type === Minor
})

const keysContainingChord = chord => {
  const keys = keySignatures.filter(key => key.chords.some(chordInKey => chordInKey === chord && key.type === Major))
               .map(key => ({
                majorKey: {key: key, intervalInKey: intervalInKey(key, chord)},
                relativeMinor: {key: relativeMinorForKey(key), intervalInKey: intervalInKey(relativeMinorForKey(key), chord)}
               }))
  keys.sort((key1, key2) => key1.majorKey.intervalInKey - key2.majorKey.intervalInKey)
  return keys
}

const intervalInKey = (key, chord) => key.chords.indexOf(chord) + 1

const circleOfFifths = (count = 12, backwards = false) => {
  const keys = []
  for (let i = 0; i < count; i++) {
    keys.push(noteSemitonesAhead(C, (backwards ? 5 : 7) * i))
  }
  return keys
}

// -------------- begin awful section --------------
