// maybe trying to programatically determine all these was just not the way to go


const orderOfKeysWithSharps = circleOfFifths(8).slice(1)
const orderOfKeysWithFlats = circleOfFifths(7, true).slice(1)

// these should not be special cases, lol, this is garbage code
// especially c minor
const accidentals = {
  Major: {C: {type: "#", count: 0}},
  Minor: {C: {type: "b", count: 3}}
}
orderOfKeysWithSharps.forEach((key, index) => { accidentals.Major[key] = {type: "#", count: index + 1} })
orderOfKeysWithFlats.forEach((key, index) => { accidentals.Major[key] = {type: "b", count: index + 1} })
orderOfKeysWithSharps.forEach(key => { accidentals.Minor[key] = accidentals[Major][noteSemitonesAhead(key, 3)] })
orderOfKeysWithFlats.forEach(key => { accidentals.Minor[key] = accidentals[Major][noteSemitonesAhead(key, 3)] })

// can we programatically determine this? i have no idea. probably.
const orderOfAccidentals = {
  "#": [F, C, G, D, A, E, B],
  "b": [B, E, A, D, G, F, C]
}

const notesInKey = ({root, type}) => {
  const accidentalsOfKey = accidentals[type][root]

  let notes = naturalNotes.map(note => (orderOfAccidentals[accidentalsOfKey.type].indexOf(note) !== -1 &&
                                          orderOfAccidentals[accidentalsOfKey.type].indexOf(note) < accidentalsOfKey.count) ?
                                          (note + accidentalsOfKey.type) : note )
  notes = sortStartingFromKey(notes, root)

  return notes
  // return parseNoteLocations(notes)
}

