const parseKeyFromString = (keySignatureString) => findKeySignature({root: keySignatureString.split("-")[0], type: keySignatureString.split("-")[1]})

const findKeySignature = ({root, type}) => keySignatures.find(key => key.root === root && key.type === type)


export {keySignatures, keysContainingChord, notesInKey, circleOfFifths, chordsInKey, parseKeyFromString, intervalInKey, findKeySignature}