import { findChord } from "../chordLogic/chords"
import { chordsInKey, circleOfFifths, findKeySignature } from "../chordLogic/keySignatures"

const keyUnlockOrder = [...circleOfFifths(6), ...circleOfFifths(7, true).slice(1)]

class User {
  constructor(existingProgression) {
    if (existingProgression === null) {
      this.masteryProgress = {chords: {Major: {}, Minor: {}}, intervals: {Major: {}, Minor: {}}}
      this.mostRecentPracticeSession = {}
    } else {
      existingProgression = JSON.parse(existingProgression)
      this.masteryProgress = existingProgression.masteryProgress
      this.mostRecentPracticeSession = existingProgression.mostRecentPracticeSession
      this.restorePracticeSessionObjectRelations()
    }
  }

  getChordsToLearnInKey = (keySignature, includeAlreadyLearned = false, noLimit = false) => {
    const keyRoot = keySignature.root
    const keyType = keySignature.type
    const alreadyLearnedChords = this.getLearnedChordsInKey(keySignature)
    const learningLimit = alreadyLearnedChords.length >= 3 ? 2 : 3 // this is pretty arbitrary but shrug
    let chords = chordsInKey(keyRoot, keyType).filter(chord => !this.hasLearnedChord(keySignature, chord))
    if (!noLimit) {
      chords = chords.slice(0, learningLimit)
    }
    return includeAlreadyLearned ? [...alreadyLearnedChords, ...chords] : chords
  }

  hasLearnedChord = ({root, type}, chord) => 
    this.chordMastery[type] &&
    this.chordMastery[type][root] &&
    this.chordMastery[type][root][chord.name]

  hasLearnedInterval = ({root, type}, interval) => 
    this.intervalMastery[type] &&
    this.intervalMastery[type][root] &&
    this.intervalMastery[type][root][interval]

  savePracticeSession = (sessionData) => {
    switch (sessionData.type) {
      case "intervals":
        const intervalsLearned = sessionData.intervalsPracticed.filter(interval => !this.hasLearnedInterval(sessionData.keySignature, interval))
        this.mostRecentPracticeSession = {
          type: sessionData.type,
          keySignature: sessionData.keySignature,
          intervalsPerfected: sessionData.intervalsPerfected.filter(interval => !intervalsLearned.includes(interval)), // can't make mastery progress on a just-learned interval
          intervalsLearned: intervalsLearned
        }
        this.mostRecentPracticeSession.intervalsLearned.forEach(interval => this.setIntervalLearned(sessionData.keySignature, interval))
        break
      case "chords":
        this.mostRecentPracticeSession = sessionData
        sessionData.chords.forEach(chord => this.setChordLearned(sessionData.keySignature, chord))
    }
    localStorage.setItem("progress", JSON.stringify({masteryProgress: this.masteryProgress, mostRecentPracticeSession: this.mostRecentPracticeSession}))
  }

  setChordLearned({root, type}, chord) {
    if (this.chordMastery[type][root] === undefined) {
      this.chordMastery[type][root] = {}
    }
    this.chordMastery[type][root][chord.name] = true
  }

  setIntervalLearned({root, type}, interval) {
    if (this.intervalMastery[type][root] === undefined) {
      this.intervalMastery[type][root] = {}
    }
    this.intervalMastery[type][root][interval] = true
  }

  getReviewData = () => this.mostRecentPracticeSession

  getLearnedChordsInKey = (keySignature) => 
    chordsInKey(keySignature.root, keySignature.type).filter(chord => this.hasLearnedChord(keySignature, chord))
  
  getLearnedIntervalsInKey = (keySignature) => {
    const intervals = []
    for (let interval = 0; interval < 7; interval++) {
      if (this.hasLearnedInterval(keySignature, interval)) {
        intervals.push(interval)
      }
    }
    return intervals
  }
  
  hasMasteredChordsInKey = (keySignature) => this.getLearnedChordsInKey(keySignature).length === chordsInKey(keySignature.root, keySignature.type).length
  
  getUnlockedKeySignatures = () => {
    let unlockedKeys = 1
    const keys = circleOfFifths()
    while (unlockedKeys <= keys.length) {
      // TODO -- don't hardcode this to major
      if (this.hasMasteredChordsInKey(findKeySignature({root: keys[unlockedKeys - 1], type: "Major"}))) {
        unlockedKeys++
      } else {
        break
      }
    }
    return keyUnlockOrder.slice(0, unlockedKeys)
  }

  get chordMastery() { return this.masteryProgress.chords }
  get intervalMastery() { return this.masteryProgress.intervals }

  restorePracticeSessionObjectRelations() {
    if (this.mostRecentPracticeSession.chords) {
      this.mostRecentPracticeSession.chords = this.mostRecentPracticeSession.chords.map(chord => findChord(chord))
    }
    this.mostRecentPracticeSession.keySignature = findKeySignature(this.mostRecentPracticeSession.keySignature)
  }
}

const loadProgress = () => new User(localStorage.getItem("progress"))

export { loadProgress }