import React, { useMemo } from 'react';
import { useState } from 'react';
import { findKeySignature } from '../chordLogic/keySignatures';
import {audioContext, useAudioOutput} from "../sound"
import { randomNumber } from '../utils/utils';
import RoundedButton from './RoundedButton';

function ListeningPracticeScreen({progress}) {
  const initialProgression = useMemo(() => [randomNumber(7) + 1, randomNumber(7) + 1], [])

  const [progression, setProgression] = useState(initialProgression)
  const key = findKeySignature({root: "C", type: "Major"})
  const chordsToPlay = progression.map(interval => key.chords[interval - 1])

  const audio = useAudioOutput()
  const audioReady = audio !== null

  const play = () => {
    if (audioReady) {
      const notePlays = chordsToPlay.reduce((notePlays, chord, index) => [...notePlays, ...chord.notes.map(note => ({time: index * 0.5, note, duration: 0.5}))], [])
      console.log(notePlays)
      // audio.play('C4')
      // audio.play('E4')
      // audio.play('G4')
      audio.schedule(audioContext.currentTime, notePlays)
    }
  }

  const [progressionsGuessed, setProgressionsGuessed] = useState({})
  const intervalClicked = (interval, progressionIndex) => {
    console.log(interval, progressionIndex)
    const correct = progression[progressionIndex] === interval + 1
    const newProgressionsGuessed = {...progressionsGuessed}
    newProgressionsGuessed[progressionIndex] = correct
    setProgressionsGuessed(newProgressionsGuessed)
  }
  console.log(progressionsGuessed)

  return (
    <div>
      <div>Key: {key.name}</div>
      {/* <ProgressionChooser progression={progression} setProgression={setProgression}/> */}
      <ProgressionQuizAnswerButtons length={progression.length} intervalClicked={intervalClicked}/>
      <button disabled={!audioReady} onClick={() => play(true)}>{audioReady ? "Play" : "Audio Loading..."}</button>
    </div>
  );
}

const ProgressionQuizAnswerButtons = ({length, intervalClicked}) => {
  const controls = []
  for (let i = 0; i < length; i++) {
    controls.push(
      <ProgressionQuizAnswerRowColumn
        key={i}
        intervalSelected={(interval) => intervalClicked(interval, i)}
      />
    )
  }
  return (
    <div style={{display: "flex", flexFlow: "row", alignItems: "center"}}>
      {controls}
    </div>
  )
}

const ProgressionQuizAnswerRowColumn = ({intervalSelected}) => {
  const intervals = []
  for (let i = 0; i < 7; i++) { intervals.push(i) }
  return (
    <div>
      {intervals.map(interval =>
        <RoundedButton enabled={true} key={interval} onClick={() => intervalSelected(interval)}>
          {interval + 1}
        </RoundedButton>
      )}
    </div>
  )
}


const ProgressionChooser = ({progression, setProgression}) => {
  const intervalSelected = (interval, progressionIndex) => {
    const newProgression = [...progression]
    newProgression[progressionIndex] = interval + 1
    setProgression(newProgression)
  }

  const addInterval = () => {
    const newProgression = [...progression, 1]
    setProgression(newProgression)
  }

  return (
    <div style={{display: "flex", flexFlow: "row", alignItems: "center"}}>
      {progression.map((interval, progressionIndex) =>
        <ProgressionControl
          key={`${progressionIndex}-${interval}`}
          activeInterval={interval}
          intervalSelected={(interval) => intervalSelected(interval, progressionIndex)}
        />
      )}
      <RoundedButton onClick={addInterval} enabled={true}>Add</RoundedButton>
    </div>
  )
}

const ProgressionControl = ({activeInterval, intervalSelected}) => {
  const intervals = []
  for (let i = 0; i < 7; i++) { intervals.push(i) }
  return (
    <div>
      {intervals.map(interval =>
        <RoundedButton enabled={interval !== activeInterval - 1} key={interval} onClick={() => intervalSelected(interval)}>
          {interval + 1}
        </RoundedButton>
      )}
    </div>
  )
}

export default ListeningPracticeScreen;