import { noteEquivalents, noteOrder, noteSemitonesAhead } from "../../chordLogic/notes"

const voicingRequestHandlers = {
  addDoubledNote: (chord, notes, voicingRequest) => [...notes, noteSemitonesAhead(chord.root, voicingRequest.value)]
}

const processVoicingRequests = (chord, notes, voicing) => {
  let revoicedNotes = [...notes]
  voicing.forEach(voicingRequest => {
    // console.log(chord, notes, voicing, voicingRequest)
    console.log(revoicedNotes)
    revoicedNotes = voicingRequestHandlers[voicingRequest.type](chord, notes, voicingRequest)
    console.log(revoicedNotes)
  })
  return revoicedNotes
}

export const chordTypes = [
  {
    name: 'Major',
    canHaveSeventh: true,
    printName: ({ root, seventh }) => `${root} Major${seventh ? ' 7th' : ''}`,
    construct: (root, { withSeventh = false } = {}) => {
      return [
        root,
        noteSemitonesAhead(root, 4),
        noteSemitonesAhead(root, 7),
        ...(withSeventh ? [noteSemitonesAhead(root, 10)] : [])
      ]
    }
  },
  {
    name: 'Minor',
    canHaveSeventh: true,
    printName: ({ root, seventh }) => `${root} Minor${seventh ? ' 7th' : ''}`,
    construct: (root, { withSeventh = false, voicing = [] } = {}) => {
      const chordNotes = processVoicingRequests({ root, type: 'Minor', seventh: withSeventh }, [
        root,
        noteSemitonesAhead(root, 3),
        noteSemitonesAhead(root, 7),
        ...(withSeventh ? [noteSemitonesAhead(root, 10)] : [])
      ], voicing)
      return chordNotes
    }
  },
  {
    name: 'Diminished',
    canHaveSeventh: true,
    printName: ({ root, seventh }) => `${root} Diminished${seventh ? ' 7th' : ''}`,
    construct: (root, { withSeventh = false } = {}) => {
      return [
        root,
        noteSemitonesAhead(root, 3),
        noteSemitonesAhead(root, 6),
        ...(withSeventh ? [noteSemitonesAhead(root, 10)] : [])
      ]
    }
  },
  {
    name: 'Augmented',
    canHaveSeventh: true,
    printName: ({ root, seventh }) => `${root} Augmented${seventh ? ' 7th' : ''}`,
    construct: (root, { withSeventh = false } = {}) => {
      return [
        root,
        noteSemitonesAhead(root, 4),
        noteSemitonesAhead(root, 8),
        ...(withSeventh ? [noteSemitonesAhead(root, 10)] : [])
      ]
    }
  },
  {
    name: 'Fr+',
    canHaveSeventh: false,
    printName: ({ root }, { shortName = false } = {}) => !shortName ? `French Augmented 6th in ${root}` : `Fr+ in ${root}`,
    construct: (root) => {
      return [
        noteSemitonesAhead(root, -4),
        root,
        noteSemitonesAhead(root, 2),
        noteSemitonesAhead(root, 6),
      ]
    }
  },
  {
    name: 'Gr+',
    canHaveSeventh: false,
    printName: ({ root }, { shortName = false } = {}) => !shortName ? `German Augmented 6th in ${root}` : `Gr+ in ${root}`,
    construct: (root) => {
      return [
        noteSemitonesAhead(root, -4),
        root,
        noteSemitonesAhead(root, 3),
        noteSemitonesAhead(root, 6),
      ]
    }
  },
  {
    name: 'Major b5',
    canHaveSeventh: true,
    printName: ({ root, seventh }) => `${root} Major b5${seventh ? ' 7th' : ''}`,
    construct: (root, { withSeventh = false } = {}) => {
      return [
        root,
        noteSemitonesAhead(root, 4),
        noteSemitonesAhead(root, 6),
        ...(withSeventh ? [noteSemitonesAhead(root, 10)] : [])
      ]
    }
  },
  {
    name: 'Fully Diminished 7th',
    canHaveSeventh: false,
    printName: ({ root }) => `${root}Âº`,
    construct: (root) => {
      return [
        root,
        noteSemitonesAhead(root, 3),
        noteSemitonesAhead(root, 6),
        noteSemitonesAhead(root, 9)
      ]
    }
  },
]

export const chordTypesByName = name => chordTypes.find(chordType => chordType.name === name)
export const constructChord = ({ root, type, seventh, voicing = [] }) => chordTypesByName(type).construct(root, { withSeventh: seventh, voicing })
export const getChordName = (chord, options) => chordTypesByName(chord.type).printName(chord, options)


export const progressions = [
  { name: "V â I", initial: "Major", resultantChord: ({ root }) => ({ root: noteSemitonesAhead(root, -7), type: "Major", seventh: false }) },
  { name: "Gr+ â i6/4", initial: "Gr+", resultantChord: ({ root }) => ({ root, type: "Minor", seventh: false, voicing: [{ type: 'addDoubledNote', value: -5 }] }) },
  { name: "Fr+ â V", initial: "Fr+", resultantChord: ({ root }) => ({ root: noteSemitonesAhead(root, 7), type: "Major", seventh: false }) },
  { name: "Vb5 â V7", initial: "Major b5", resultantChord: ({ root }) => ({ root, type: "Major", seventh: true }) },
  { name: "V+ â I6/4", initial: "Augmented", resultantChord: ({ root }) => ({ root: noteSemitonesAhead(root, -7), type: "Major", seventh: false }) },
  { name: "ivÂº â V", initial: "Fully Diminished 7th", resultantChord: ({ root }) => ({ root: noteSemitonesAhead(root, 1), type: "Major", seventh: false }) },
]


export const getEquivalentsToChord = ({ root, type, seventh = false }) => {
  const alternativeChords = []

  const alternativeRoots = noteOrder
  const alternativeChordTypes = chordTypes.map(type => type.name)

  alternativeRoots.forEach(alternativeRoot => {
    alternativeChordTypes.forEach(alternativeType => {
      const seventhOptions = chordTypesByName(alternativeType).canHaveSeventh ? [true, false] : [false]
      seventhOptions.forEach(alternativeIsSeventh => {
        if (alternativeRoot === root && alternativeType === type && (alternativeIsSeventh === seventh || !chordTypesByName(type).canHaveSeventh)) { return }

        const alternativeChord = { root: alternativeRoot, type: alternativeType, seventh: alternativeIsSeventh }
        if (chordsAreEquivalent({ root, type, seventh }, alternativeChord)) {
          alternativeChords.push(alternativeChord)
        }
      })
    })
  })

  return alternativeChords
}

const chordsAreEquivalent = (chord1, chord2) => {
  const chord1Notes = constructChord(chord1)
  const chord2Notes = constructChord(chord2)
  return chord1Notes.length === chord2Notes.length && chord1Notes.every(note => chordContainsNote(chord2Notes, note))
}

const chordContainsNote = (chordNotes, note) => {
  return chordNotes.some(chordNote => chordNote === note || noteEquivalents[chordNote] === note)
}

export const chordsAreIdentical = (chord1, chord2) => {
  return ['type', 'root', 'seventh'].every(property => chord1[property] === chord2[property])
}


export const getProgressionOptions = (chord) => {
  const equivalents = getEquivalentsToChord(chord)
  const startingChords = [chord, ...equivalents]
  const progressionOptions = []
  startingChords.forEach(startingChord => {
    progressions.forEach(progression => {
      if (progression.initial === startingChord.type) {
        progressionOptions.push({ startingChord, resultantChord: progression.resultantChord(startingChord), name: progression.name })
      }
    })
  })
  return progressionOptions
}