import React, { useEffect, useState } from 'react';
import MidiNumbers from '../../react-piano/MidiNumbers';
import { useReusableDelay, useStabilizedValue } from '../../utils/hooks';

const Pitchfinder = require("pitchfinder");
const detectPitch = Pitchfinder.AMDF();

const findPitch = (myAudioBuffer) =>  {
  const float32Array = myAudioBuffer.getChannelData(0); // get a single channel of sound
  const pitch = detectPitch(float32Array); // null if pitch cannot be identified
  const note = noteFromPitch(pitch)
  const noteName = (note !== undefined && note >= MidiNumbers.MIN_MIDI_NUMBER && note <= MidiNumbers.MAX_MIDI_NUMBER) ? MidiNumbers.getAttributes(note).note : null
  return noteName
}

function noteFromPitch( frequency ) {
	var noteNum = 12 * (Math.log( frequency / 440 )/Math.log(2) );
	return Math.round( noteNum ) + 69;
}

const noteNameFromMidiNumber = (number) => {
  number = number % 12
  const notes = ["C", "C#/Db", "D", "D#/Eb", "E", "F", "F#/Gb", "G", "G#/Ab", "A", "A#/Bb", "B"]
  return notes[number]
}

const useAudioInputRaw = () => {
  const [pitch, setPitch] = useState(null)

  useEffect(() => {
    navigator.mediaDevices.getUserMedia({ audio: true, video: false })
    .then((stream) => {
      const audioContext = new AudioContext();
      const source = audioContext.createMediaStreamSource(stream);
      const processor = audioContext.createScriptProcessor(1024, 1, 1);
  
      source.connect(processor);
      processor.connect(audioContext.destination);
  
      processor.onaudioprocess = function(event) {
        // Do something with the data, e.g. convert it to WAV
        // console.log(e.inputBuffer);
        setPitch(findPitch(event.inputBuffer))
      }
    })
  }, [])

  return pitch; 
}

export const useAudioInput = () => {
  const rawPitch = useAudioInputRaw()
  const stabilizationPeriod = 50
  const stabilizedPitch = useStabilizedValue(rawPitch, stabilizationPeriod)
  return stabilizedPitch
}