import React, { useState } from 'react';
import KeySignatureCircle from './KeySignatureCircle';
import ChordProgressList from './ChordProgressList';
import { findKeySignature } from '../chordLogic/keySignatures';

function ProgressScreen({progress}) {

  const unlockedKeys = progress.getUnlockedKeySignatures()

  const [keyMousedOver, setKeyMousedOver] = useState(null)

  return (
    <div style={{width: "100%", height: "100vh", display: "flex", justifyContent: "center", alignItems: "center"}}>
      <div style={{display: "flex", flexFlow: "column"}}>
        <div style={{display: "flex", justifyContent: "center", fontSize: "25px", marginBottom: "25px"}}>Scales</div>
        <KeySignatureCircle onKeyMousedOver={setKeyMousedOver} unlockedKeys={unlockedKeys}/>
      </div>
      <div style={{visibility: keyMousedOver !== null ? "visible" : "hidden", width: "500px", transform: "scale(0.75)"}}>
        {keyMousedOver !== null ? <ChordProgressList
          // TODO -- don't hardcode this to major
          keySignature={findKeySignature({root: keyMousedOver, type: "Major"})}
          previouslyMasteredChords={progress.getLearnedChordsInKey({root: keyMousedOver, type: "Major"})}
          mode={"chords"}
        /> : null}
      </div>
    </div>
  );
}

export default ProgressScreen;