import React from 'react';
import Piano from '../react-piano/Piano';
import MidiNumbers from '../react-piano/MidiNumbers';
import '../react-piano/styles.css';
import './pianoStyles.css';
import { useEffect } from 'react';
import { useState } from 'react';

const firstNote = MidiNumbers.fromNote('c3');
const lastNote = MidiNumbers.fromNote('f5');

const PianoGraphic = ({activeNotes, keysAreCorrect, correctNotes, keyPlayPulses, highlightedNotes, renderScale, intervalOfChord, activeNotesTutorial = []}) => {
  const [intervalMarkersToShow, setIntervalMarkersToShow] = useState(-1) // wait a single delay loop before spawning the first one
  useEffect(() => {
    let currentIntervalMarkersToShow = intervalMarkersToShow
    if (intervalOfChord !== null) {
      const markerSpawningInterval = setInterval(() => {
        currentIntervalMarkersToShow++
        setIntervalMarkersToShow(currentIntervalMarkersToShow)
        if (currentIntervalMarkersToShow >= intervalOfChord) {
          clearInterval(markerSpawningInterval)
        }
      }, 400)
    }
  }, [intervalOfChord])

  // account for notes without velocities
  activeNotes = activeNotes.length > 0 && activeNotes[0].velocity === undefined ? activeNotes.map(note => ({key: note, velocity: 1})) : activeNotes

  return (
    <div className="piano-wrapper">
      <Piano
        noteRange={{ first: firstNote, last: lastNote }}
        playNote={(midiNumber) => {
          // Play a given note - see notes below
        }}
        stopNote={(midiNumber) => {
          // Stop playing a given note - see notes below
        }}
        width={1000 * renderScale}
        activeNotes={activeNotes}
        correctNotes={correctNotes}
        keysAreCorrect={keysAreCorrect}
        keyPlayPulses={keyPlayPulses}
        highlightedNotes={highlightedNotes}
        intervalOfChord={intervalOfChord}
        intervalMarkersToShow={intervalMarkersToShow}
        activeNotesTutorial={activeNotesTutorial}
      />
    </div>
  )
}

export default PianoGraphic;