/** @jsx jsx */
/** @jsxFrag React.Fragment */
import { jsx, css } from '@emotion/react'

import React from 'react';
import { combineStyles } from '../utils/utils';

function RoundedButton({ onClick, children, enabled, styles, className = "", active = false }) {

  return (
    <div css={css`
      height: 40px;
      min-width: fit-content;
      width: 120px;
      padding: 0px 8px;
      border: 2px solid #fff0;
      border-radius: 12px;
      margin: 10px;
      display: flex;
      align-items: center;
      justify-content: center;
      color: hsl(200, 100%, 60%);
      text-shadow: #00aaff 0px 0px 10px;
      font-weight: bold;
      /* background-color: #fff8dc18; */
      transition: border 0.2s, color 0.2s, background-color 0.2s;
      user-select: none;
      font-size: 18px;

      ${enabled && css`
        border-color: #ffffff08;
        color: #fffb;
        background-color: #fff8dc10;
        cursor: pointer;
        text-shadow: none;

        &:hover, &:active {
          transition: none;
          background-color: #fff8dc28;
          border-color:#fff3;
          color: #fff;
          border-style: outset;
        }

        &:active {
          border-style: inset;
        }
      `}

      ${active && css`
        border-color: rgba(255, 255, 255, 0.75);
        color: white;
        /* border-width: 1px; */
      `}
      
    `} onClick={onClick}>
      {children}
    </div>
  );
}

export default RoundedButton;