import {noteOrder, chordTypes, noteSemitonesAhead, parseNoteLocations} from './notes'
import {intervalInKey} from './keySignatures'

const { Major, Minor, Diminished } = chordTypes

const intervalLetters = [
  "i", "ii", "iii", "iv", "v", "vi", "vii"
]

class Chord {
  constructor(root, type, isSeventh) {
    let notes = [
      root,
      noteSemitonesAhead(root, type === Major ? 4 : 3),
      noteSemitonesAhead(root, type === Diminished ? 6 : 7)
    ]
    if (isSeventh) {
      notes.push(noteSemitonesAhead(root, type === Major ? 11 : 10))
    }
    notes = parseNoteLocations(notes) 
  
    this.name = `${root} ${type}${isSeventh ? " 7th" : ""}`
    this.notes = notes
    this.root = root
    this.type = type
    this.keyCount = isSeventh ? "7th" : "Triad"
  }

  intervalLetterInKey = (keySignature) => {
    const letter = intervalLetters[intervalInKey(keySignature, this) - 1]
    switch (this.type) {
      case Major:
        return letter.toUpperCase()
      case Minor:
        return letter
      case Diminished:
        return letter + "Â°"
    }
  }
}

const chords = []
noteOrder.forEach(root => {
  [Major, Minor, Diminished].forEach(type => {
    chords.push(new Chord(root, type, false))
  })
})

const findChord = ({root, type}) => chords.find(chord => chord.root === root && chord.type === type)

export { chords, findChord }