/** @jsxFrag React.Fragment */
import { jsx, css } from '@emotion/react'
import React, { createContext, forwardRef, useContext, useRef, useState } from "react"
import { noteEquivalents, noteOrder, parseNoteLocations } from '../../chordLogic/notes';
import PianoGraphic from "../PianoGraphic"
import RoundedButton from '../RoundedButton';
import { chordsAreIdentical, chordTypes, chordTypesByName, constructChord, getChordName, getEquivalentsToChord, getProgressionOptions } from "./logic";
import Xarrow from "react-xarrows";
import { audioContext, useAudioOutput } from '../../sound';

const possibleRoots = noteOrder.map(note => ({ note, fullName: noteEquivalents[note] !== undefined ? `${note}/${noteEquivalents[note]}` : note }))
const possibleChordTypes = chordTypes.map(({ name }) => name)

const TheoryContext = createContext(null)

export const TheoryScreen = ({ }) => {
  const [selectedChord, setSelectedChord] = useState({ root: 'C', type: 'Major', seventh: false })
  const [arpeggiatePlayback, setArpeggiatePlayback] = useState(false)

  const equivalents = getEquivalentsToChord(selectedChord)
  const progressionOptions = getProgressionOptions(selectedChord)
  // console.log(progressionOptions)

  const audio = useAudioOutput()

  const startingChordRef = useRef(null)
  
  // console.log(chordNotes)
  // console.log(getEquivalentsToChord(selectedChord))
  // equivalents.forEach(equivalent => console.log(chordTypesByName(equivalent.type).construct(equivalent.root)))
  return (
    <TheoryContext.Provider value={{ setSelectedChord, arpeggiatePlayback, audio }}>
      <div css={css`display: flex; flex-direction: column; justify-content: center; align-items: center; width: 100vw%;`}>
        <div css={css`width: 100%; display: flex;`}>
          {possibleRoots.map(({ note: root, fullName }) => <RoundedButton key={root} enabled={true} onClick={() => setSelectedChord(chord => ({ ...chord, root }))} active={selectedChord.root === root}>{fullName}</RoundedButton>)}
        </div>
        <div css={css`display: flex; align-items: center;`}>
          {possibleChordTypes.map(type => <RoundedButton key={type} enabled={true} onClick={() => setSelectedChord(chord => ({ ...chord, type }))} active={selectedChord.type === type}>{type}</RoundedButton>)}
          <div>
            <input type="checkbox" checked={selectedChord.seventh} onChange={() => setSelectedChord(chord => ({ ...chord, seventh: !chord.seventh }))} /> 7th
          </div>
        </div>
        <div>
          <input type="checkbox" checked={arpeggiatePlayback} onChange={() => setArpeggiatePlayback(arpeggiate => !arpeggiate)} /> Arpeggiate Playback
        </div>
        {/* <div>{JSON.stringify(equivalents)}</div> */}
        <div css={css`display: flex; gap: 80px;`}>
          <ChordDisplay chord={selectedChord} ref={startingChordRef} />
          <ProgressionOptions chord={selectedChord} startingChordRef={startingChordRef} />
        </div>
      </div>
    </TheoryContext.Provider>
  )
}

const getHumanizedNoteTimeOffset = () => Math.random() * 0.04

const ChordDisplay = forwardRef(({ chord, label = null }, ref) => {
  const { setSelectedChord, arpeggiatePlayback, audio } = useContext(TheoryContext)
  const delayBetweenArpeggiatedNotes = 0.3
  const totalPlaybackDuration = arpeggiatePlayback ? 1.5 : 1

  const chordTitle = chordTypesByName(chord.type).printName(chord)
  const chordNotes = constructChord(chord)
  const chordNoteLocations = parseNoteLocations(chordNotes)

  const audioReady = audio !== null
  const play = () => {
    if (audioReady) {
      const notePlays = chordNoteLocations.reduce((notePlays, note, index) => {
        const delay = arpeggiatePlayback ? (index * delayBetweenArpeggiatedNotes) : getHumanizedNoteTimeOffset()
        return [...notePlays, ({ time: delay, note, duration: totalPlaybackDuration - delay })]
      }, [])
      audio.schedule(audioContext.currentTime, notePlays)
    }
  }

  return (
    <div onMouseEnter={play} onClick={() => setSelectedChord(chord)} css={css`
      display: flex;
      flex-direction: column;
      align-items: center;
      padding: 12px;
      border-radius: 4px;
      height: fit-content;

      cursor: pointer;
      transition: transform 0.5s cubic-bezier(.3,1.85,.7,.83), box-shadow 0.2s, background-color 0.2s;
      &:hover, &:active {
        transform: scale(1.03);
        box-shadow: 0px 0px 3px 1px rgba(255, 255, 255, 0.15);
        background-color: rgba(255, 255, 255, 0.05);
      }
    `}>
      <div css={css`position: relative; width: 100%; display: flex; justify-content: center;`}>
        <div css={css`position: absolute; top: 0px; left: 0px; width: 25%; height: 100%; display: flex; justify-content: center; align-items: flex-end;`}>
          <div css={css`padding-bottom: 6px;`}>{label}</div>
        </div>
        <h2>{chordTitle}</h2>
      </div>
      <div ref={ref}>
        <PianoGraphic
          activeNotes={chordNoteLocations}
          correctNotes={chordNoteLocations}
          keysAreCorrect={null}
          keyPlayPulses={false}
          highlightedNotes={[]}
          renderScale={0.5}
          intervalOfChord={null}
        />
      </div>
    </div>
  )
})

const ProgressionOptions = ({ chord, startingChordRef }) => {
  const progressionOptions = getProgressionOptions(chord)
  console.log(progressionOptions)

  return (
    <div>
      {progressionOptions.map(progression => <ProgressionDisplay key={`${progression.name}${progression.startingChord.root}`} startingChordRef={startingChordRef} progression={progression} originalChord={chord} />)}
    </div>
  )
}

const ProgressionDisplay = ({ startingChordRef, progression, originalChord }) => {
  const { startingChord, resultantChord, name } = progression
  const resultantChordRef = useRef(null)

  const label = (
    <div css={css`display: flex; flex-direction: column; align-items: center;`}>
      {!chordsAreIdentical(originalChord, startingChord) && <div css={css`opacity: 0.7; font-size: 14px;`}>
        {`as ${getChordName(startingChord, { shortName: true })}`}
      </div>}
      <div>
        {name}
      </div>
    </div>
  )

  return (
    <>
      <ChordDisplay chord={resultantChord} ref={resultantChordRef} label={label} />
      <div>
        <Xarrow
          start={startingChordRef}
          end={resultantChordRef}
          endAnchor={{ position: "auto", offset: { x: -8 } }}
          startAnchor={{ position: "auto", offset: { x: 8 } }}
        />
      </div>
    </>
  )
}