import { noteNames } from "../chordLogic/notes"

const {A, B, C, D, E, F, G, As, Cs, Ds, Fs, Gs, Ab, Bb, Db, Eb, Gb} = noteNames

const possibleTones = [C, D, E, F, G, A, B]
const possibleOctaves = ["3", "4", "5"]
const clefs = [
  {
    name: "treble",
    possibleOctaves: ["3", "4", "5"]
  },
  {
    name: "bass",
    possibleOctaves: ["2", "3", "4"]
  }
]
export const possibleNotes = possibleTones.reduce((list, tone) => {
  clefs.forEach(clef => 
    clef.possibleOctaves.forEach(octave =>
      list.push({note: tone, octave, clef: clef.name})
    )
  )
  return list
}, [])