import { useEffect, useRef, useState } from "react"

export const usePrevious = (value) => {
  const ref = useRef();
  useEffect(() => {
    ref.current = value;
  });
  return ref.current;
}

export const useIncrementer = (initialValue, limit, intervalPeriod, startCondition = true) => {
  const [value, setValue] = useState(initialValue)
  useEffect(() => {
    if (startCondition) {
      let currentValue = value
      const interval = setInterval(() => {
        currentValue++
        setValue(currentValue)
        if (currentValue >= limit) {
          clearInterval(interval)
        }
      }, intervalPeriod)
    }
  }, [intervalPeriod, limit, startCondition])
  return value
}

export const useDelay = (delay, startCondition = true) => {
  const [ready, setReady] = useState(false)
  useEffect(() => {
    if (startCondition) {
      setTimeout(() => {
        setReady(true)
      }, delay)
    }
  }, [delay, startCondition])
  return ready
}

export const useSequence = (delays, startCondition = true) => {
  const [counter, setCounter] = useState(0)
  useEffect(() => {
    if (startCondition && counter < delays.length) {
      setTimeout(() => {
        setCounter(counter + 1)
      }, delays[counter])
    }
  }, [delays, startCondition, counter])
  return delays.map((_, index) => counter > index)
}

export const useAwait = (promise) => {
  const [value, setValue] = useState(null)
  useEffect(() => {
    (async () => {
      setValue(await promise)
    })();
  }, []);
  return value
}

export const useReusableDelay = (delay, startCondition = true) => {
  const [ready, setReady] = useState(false)
  const [restartedAt, setRestartedAt] = useState(null)
  const timeout = useRef(null)

  const cancel = () => {
    if (timeout.current !== null) {
      clearTimeout(timeout.current)
    }
  }

  useEffect(() => {
    if (timeout.current !== null) {
      clearTimeout(timeout.current)
    }
    if (startCondition) {
      timeout.current = setTimeout(() => {
        setReady(true)
        timeout.current = null
      }, delay)
    } else {
      setReady(false)
    }

    return cancel
  }, [delay, startCondition, restartedAt])

  const restart = () => {
    setReady(false)
    setRestartedAt(Date.now())
  }
  return [ready, restart, cancel]
}

export const useStabilizedValue = (rawValue, stabilizationPeriod) => {
  const [stabilizedValue, setStabilizedValue] = useState(rawValue)
  const [stabilized, valueChanged] = useReusableDelay(stabilizationPeriod)
  useEffect(() => {
    valueChanged()
  }, [rawValue])
  useEffect(() => {
    if (stabilized) {
      setStabilizedValue(rawValue)
    }
  }, [stabilized])

  return stabilizedValue
}