import React, { useState } from 'react';
import { useHistory, useParams } from 'react-router-dom';
import { parseKeyFromString } from '../chordLogic/keySignatures';
import IntervalsTutorial from './IntervalsTutorial';
import ProgressionsTutorial from './ProgressionsTutorial';

function LearningIntervalsScreen({progress}) {
  const { keySignatureString } = useParams()
  const keySignature = parseKeyFromString(keySignatureString)

  const history = useHistory();
  const goToPracticeScreen = () => {
    history.push(`/practice/${keySignatureString}/intervals`)
  }

  const [currentTutorial, setCurrentTutorial] = useState("progressions")
  const intervalsTutorialFinished = () => {
    setCurrentTutorial("progressions")
  }
  const progressionsTutorialFinished = () => {
    setCurrentTutorial("finished")
  }
  
  const practiceButtonEnabled = currentTutorial === "finished"

  const styles = {
    practiceButton: {
      width: "100px",
      transition: "opacity 0.5s cubic-bezier(0.390, 0.575, 0.565, 1.000)",
      opacity: practiceButtonEnabled ? 1 : 0,
      visibility: practiceButtonEnabled ? "visible" : "hidden"
    }
  }
  
  const tutorialComponents = {
    intervals: <IntervalsTutorial keySignature={keySignature} tutorialFinished={intervalsTutorialFinished}/>,
    progressions: <ProgressionsTutorial keySignature={keySignature} tutorialFinished={progressionsTutorialFinished}/>,
    finished: null
  }

  return (
    <div>
      {tutorialComponents[currentTutorial]}
      <button style={styles.practiceButton} onClick={goToPracticeScreen}>Practice</button>
    </div>
  )
}

export default LearningIntervalsScreen;