import MidiNumbers from '../react-piano/MidiNumbers';

const A = 'A'
const B = 'B'
const C = 'C'
const D = 'D'
const E = 'E'
const F = 'F'
const G = 'G'

const As = 'A#'
const Cs = 'C#'
const Ds = 'D#'
const Fs = 'F#'
const Gs = 'G#'

const Ab = 'Ab'
const Bb = 'Bb'
const Db = 'Db'
const Eb = 'Eb'
const Gb = 'Gb'

const Major = "Major"
const Minor = "Minor"
const Diminished = "Diminished"

const noteNames = {A, B, C, D, E, F, G, As, Cs, Ds, Fs, Gs, Ab, Bb, Db, Eb, Gb}
const noteOrder = [C, Cs, D, Ds, E, F, Fs, G, Gs, A, As, B]
const noteEquivalents = {
  [Db]: Cs, [Eb]: Ds, [Gb]: Fs, [Ab]: Gs, [Bb]: As,
  [Cs]: Db, [Ds]: Eb, [Fs]: Gb, [Gs]: Ab, [As]: Bb,
 }
const chordTypes = {Major, Minor, Diminished}

const indexOfNote = note => noteOrder.indexOf(note) !== -1 ? noteOrder.indexOf(note) : noteOrder.indexOf(noteEquivalents[note])

const noteSemitonesAhead = (note, interval) => {
  let newIndex = indexOfNote(note) + interval
  while (newIndex < 0) {
    newIndex += 12
  }
  if (newIndex >= noteOrder.length) {
    newIndex -= noteOrder.length * Math.floor(newIndex / noteOrder.length)
  }
  // console.log("HI!", note, interval, newIndex)
  return noteOrder[newIndex]
}

// this function could probably be written more cleanly
const parseNoteLocations = (notes) => {
  let rearrangedNotes = notes.map((note, notePosition) => setNotePosition(note, notePosition, notes))
  // some silly stuff to re-shift things upward if the whole collection of notes has slipped down the staff
  // i uhhhhh this approach really makes no sense.
  // if (rearrangedNotes.filter(note => note[note.length - 1] === "3").length >= 1) {
  //   rearrangedNotes = rearrangedNotes.map(note => note.slice(0, note.length - 1) + (parseInt(note[note.length - 1], 10) + 1))
  // }
  return rearrangedNotes.map(note => MidiNumbers.fromNote(note))
}

const setNotePosition = (note, notePosition, chordNotes) => {
  if (chordHasLowerNotesAhead(note, notePosition, chordNotes)) {
    return note + "3"
  }
  return note + "4"
}

const chordHasLowerNotesAhead = (note, notePosition, chordNotes) => {
  return chordNotes.slice(notePosition + 1).some(otherNote => noteIsLower(otherNote, note))
}

const noteIsLower = (note, comparisonNote) => {
  return indexOfNote(note) < indexOfNote(comparisonNote)
}

const naturalNotes = noteOrder.filter(note => note.length === 1)

const notesAreEqual = (note1, note2) => {
  note1 = noteEquivalents[note1] || note1
  note2 = noteEquivalents[note2] || note2
  return note1 === note2
}

const sortStartingFromKey = (notes, root) => {
  if (!notes.some(note => notesAreEqual(root, note))) {
    throw new Error("Tried to sort notes by a root note not present in them, this doesn't seem legit.")
  }
  const sortedNotes = [...notes]
  while (!notesAreEqual(root, sortedNotes[0])) {
    sortedNotes.push(sortedNotes.shift())
  }
  return sortedNotes
}

export { noteNames, noteOrder, chordTypes, naturalNotes, noteSemitonesAhead, parseNoteLocations, sortStartingFromKey, noteEquivalents }