import React from 'react';
import PropTypes from 'prop-types';
import range from 'just-range';
import classNames from 'classnames';

import Key from './Key';
import MidiNumbers from './MidiNumbers';
import TransitionWrapper from '../components/TransitionWrapper';

class Keyboard extends React.Component {
  static propTypes = {
    noteRange: noteRangePropType,
    activeNotes: PropTypes.arrayOf(PropTypes.shape({
      key: PropTypes.number.isRequired,
      velocity: PropTypes.number.isRequired,
    })),
    onPlayNoteInput: PropTypes.func.isRequired,
    onStopNoteInput: PropTypes.func.isRequired,
    renderNoteLabel: PropTypes.func.isRequired,
    keyWidthToHeight: PropTypes.number.isRequired,
    className: PropTypes.string,
    disabled: PropTypes.bool,
    gliss: PropTypes.bool,
    useTouchEvents: PropTypes.bool,
    // If width is not provided, must have fixed width and height in parent container
    width: PropTypes.number,
  };

  static defaultProps = {
    disabled: false,
    gliss: false,
    useTouchEvents: false,
    keyWidthToHeight: 0.33,
    renderNoteLabel: () => {},
    highlightedNotes: []
  };

  // Range of midi numbers on keyboard
  getMidiNumbers() {
    return range(this.props.noteRange.first, this.props.noteRange.last + 1);
  }

  getNaturalKeyCount() {
    return this.getMidiNumbers().filter((number) => {
      const { isAccidental } = MidiNumbers.getAttributes(number);
      return !isAccidental;
    }).length;
  }

  // Returns a ratio between 0 and 1
  getNaturalKeyWidth() {
    return 1 / this.getNaturalKeyCount();
  }

  getWidth() {
    return this.props.width ? this.props.width : '100%';
  }

  getHeight() {
    if (!this.props.width) {
      return '100%';
    }
    const keyWidth = this.props.width * this.getNaturalKeyWidth();
    return `${keyWidth / this.props.keyWidthToHeight}px`;
  }

  render() {
    const naturalKeyWidth = this.getNaturalKeyWidth();
    return (
      <div
        className={classNames('ReactPiano__Keyboard', this.props.className)}
        style={{ width: this.getWidth(), height: this.getHeight() }}
      >
        {this.getMidiNumbers().map((midiNumber) => {
          const playedNoteTutorial = this.props.activeNotesTutorial.find(note => note.key === midiNumber)
          const playedNote = playedNoteTutorial || this.props.activeNotes.find(note => note.key === midiNumber)

          const { note, isAccidental } = MidiNumbers.getAttributes(midiNumber);
          const isActive = !this.props.disabled && playedNote !== undefined
          const isIncorrect = !this.props.disabled && !this.props.correctNotes.includes(midiNumber)
          const isConfirmedCorrect = !this.props.disabled && this.props.correctNotes.includes(midiNumber) && (this.props.keysAreCorrect || playedNoteTutorial !== undefined)
          const isHighlighted = !this.props.disabled && !isActive && this.props.highlightedNotes.includes(midiNumber)
          
          const intervalOfNote = this.props.highlightedNotes.indexOf(midiNumber)
          const displayInterval = intervalOfNote !== -1 &&
                                  intervalOfNote < this.props.intervalOfChord &&
                                  this.props.intervalMarkersToShow >= intervalOfNote
          return (
            <Key
              naturalKeyWidth={naturalKeyWidth}
              midiNumber={midiNumber}
              noteRange={this.props.noteRange}
              active={isActive}
              incorrect={isIncorrect}
              confirmedCorrect={isConfirmedCorrect}
              highlighted={isHighlighted}
              accidental={isAccidental}
              disabled={this.props.disabled}
              onPlayNoteInput={this.props.onPlayNoteInput}
              onStopNoteInput={this.props.onStopNoteInput}
              gliss={this.props.gliss}
              useTouchEvents={this.props.useTouchEvents}
              key={midiNumber}
              velocity={playedNote !== undefined ? playedNote.velocity : null}
              keyPlayPulses={this.props.keyPlayPulses}
            >
              <TransitionWrapper diffData={displayInterval} scale={1} transitionTime={0.85} transitionType="fadeOutOnly">
                {displayInterval ? <IntervalLabel keyboardWidth={this.getWidth()} interval={intervalOfNote} isActive={isActive} isAccidental={isAccidental}/> : null}
              </TransitionWrapper>
            </Key>
          );
        })}
      </div>
    );
  }
}

const IntervalLabel = ({interval, isActive, isAccidental, keyboardWidth}) => (
  <div
    className={classNames('ReactPiano__NoteLabel', {
      'ReactPiano__NoteLabel--active': isActive,
      'ReactPiano__NoteLabel--accidental': isAccidental,
      'ReactPiano__NoteLabel--natural': !isAccidental,
    })}
    style={{fontSize: keyboardWidth === 1000 ? "22px" : "16px"}} // this is comically hackily specific and i don't care
  >
    {interval + 1}
  </div>
)

function isNaturalMidiNumber(value) {
  if (typeof value !== 'number') {
    return false;
  }
  return MidiNumbers.NATURAL_MIDI_NUMBERS.includes(value);
}

function noteRangePropType(props, propName, componentName) {
  const { first, last } = props[propName];
  if (!first || !last) {
    return new Error(
      `Invalid prop ${propName} supplied to ${componentName}. ${propName} must be an object with .first and .last values.`,
    );
  }
  if (!isNaturalMidiNumber(first) || !isNaturalMidiNumber(last)) {
    return new Error(
      `Invalid prop ${propName} supplied to ${componentName}. ${propName} values must be valid MIDI numbers, and should not be accidentals (sharp or flat notes).`,
    );
  }
  if (first >= last) {
    return new Error(
      `Invalid prop ${propName} supplied to ${componentName}. ${propName}.first must be smaller than ${propName}.last.`,
    );
  }
}

export default Keyboard;
