import React from 'react';
import classNames from 'classnames';

import MidiNumbers from './MidiNumbers';
import { useState } from 'react';

const Key = (props) => {
  const {
    naturalKeyWidth,
    accidentalWidthRatio = 0.65,
    midiNumber,
    gliss,
    useTouchEvents,
    accidental,
    active,
    confirmedCorrect,
    incorrect,
    highlighted,
    velocity,
    keyPlayPulses,
    disabled,
    children,
    pitchPositions = {
      C: 0,
      Db: 0.55,
      D: 1,
      Eb: 1.8,
      E: 2,
      F: 3,
      Gb: 3.5,
      G: 4,
      Ab: 4.7,
      A: 5,
      Bb: 5.85,
      B: 6,
    }
  } = props

  const onPlayNoteInput = () => {
    props.onPlayNoteInput(midiNumber);
  };

  const onStopNoteInput = () => {
    props.onStopNoteInput(midiNumber);
  };

  // Key position is represented by the number of natural key widths from the left
  function getAbsoluteKeyPosition(midiNumber) {
    const OCTAVE_WIDTH = 7;
    const { octave, pitchName } = MidiNumbers.getAttributes(midiNumber);
    const pitchPosition = pitchPositions[pitchName];
    const octavePosition = OCTAVE_WIDTH * octave;
    return pitchPosition + octavePosition;
  }

  function getRelativeKeyPosition(midiNumber) {
    return (
      getAbsoluteKeyPosition(midiNumber) -
      getAbsoluteKeyPosition(props.noteRange.first)
    );
  }


  const pulseColors = {
    incorrect: "255, 0, 47",
    active: "0, 170, 255"
  }
  const pulseColor = incorrect ? pulseColors.incorrect : pulseColors.active
  const [alreadyActiveVelocity, setAlreadyActiveVelocity] = useState(0)
  const [pulseStyle, setPulseStyle] = useState({})
  if (active && alreadyActiveVelocity < velocity) {
    setAlreadyActiveVelocity(velocity)
    setPulseStyle({boxShadow: `0 0 0 0 rgba(${pulseColor}, 0.7)`, transition: "box-shadow 0ms, background-color 500ms"})
    setTimeout(() => {
      setPulseStyle({boxShadow: `0 0 20px ${20 * velocity / 60}px rgba(${pulseColor}, 0)`, transition: "box-shadow 400ms, background-color 500ms"})
    }, 0)
  } else if (!active && alreadyActiveVelocity > 0) {
    setAlreadyActiveVelocity(0)
    setPulseStyle({transition: "box-shadow 400ms, background-color 500ms"})
  }

  // Need to conditionally include/exclude handlers based on useTouchEvents,
  // because otherwise mobile taps double fire events.
  return (
    <div
      className={classNames('ReactPiano__Key', {
        'ReactPiano__Key--accidental': accidental,
        'ReactPiano__Key--natural': !accidental,
        'ReactPiano__Key--disabled': disabled,
        'ReactPiano__Key--active': active,
        'ReactPiano__Key--correct': confirmedCorrect,
        'ReactPiano__Key--incorrect': incorrect && active,
        'ReactPiano__Key--highlighted': highlighted,
      })}
      style={{
        left: ratioToPercentage(getRelativeKeyPosition(midiNumber) * naturalKeyWidth),
        width: ratioToPercentage(
          accidental ? accidentalWidthRatio * naturalKeyWidth : naturalKeyWidth,
        ),
        ...(keyPlayPulses ? pulseStyle : {})
      }}
      onMouseDown={useTouchEvents ? null : onPlayNoteInput}
      onMouseUp={useTouchEvents ? null : onStopNoteInput}
      onMouseEnter={gliss ? onPlayNoteInput : null}
      onMouseLeave={onStopNoteInput}
      onTouchStart={useTouchEvents ? onPlayNoteInput : null}
      onTouchCancel={useTouchEvents ? onStopNoteInput : null}
      onTouchEnd={useTouchEvents ? onStopNoteInput : null}
    >
      <div className="ReactPiano__NoteLabelContainer">{children}</div>
    </div>
  );
}

function ratioToPercentage(ratio) {
  return `${ratio * 100}%`;
}

export default Key;
